.dialogs {
  display: flex;
  align-items: flex-start;
  gap: 2rem;
  border-radius: 0.5rem;
  flex-wrap: wrap;
}

.dialogs_items {
}

.dialog.active a {
  color: red;
}

.messages {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.dialogs .newPostBlock {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  width: 100%;
  margin-bottom: 0.5rem;
  position: relative;
}

.dialogs .newPostBlock textarea {
  height: 3rem;
  border-radius: 0.5rem;
  width: 100%;
  box-sizing: border-box;
}

.dialogs .newPostBlock button {
  cursor: pointer;
  height: 2rem;
  border: 1px solid #efeae1;
  border-radius: 0.5rem;
}
